* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', sans-serif;
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.85));
  padding: 25px;
}

.app {
  background-image: url('cold.jpg');
  background-size: cover;
  background-position: bottom;
  transition: .4s ease;
}

.app.warm {
  background-image: url('sunny.jpeg');
}

.app.snowy {
  background-image: url('snowy.jpeg');
}

.app.rainy {
    background-image: url('rainy.jpg');
}

.search-box {
  width: 100%;
  margin: 30px 0 75px;
}

.search-box .search-bar {
  width: 100%;
  padding: 15px;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px 16px 16px 16px;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.05);
  border: none;
  display: block;
  outline: none;
  color: #5F5F5F;
  font-size: 20px;
  transition: .4s ease;
}

.search-box .search-bar:focus {
  background-color: rgba(225, 225, 225, 0.75);
}

.location-box .location {
  color: #FFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(30, 30, 40, 0.2);
}

.location-box .date {
  color: #FFF;
  font-size: 20px;
  font-weight: 300;
  padding-top: 20px;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 50, 0.2);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 35px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  padding: 20px 30px;

  text-align: center;
  color: #FFF;
  font-size: 110px;
  font-weight: 900;
  text-shadow: 3px 3px rgba(30, 30, 70, 0.2);
  box-shadow: 3px 3px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #FFF;
  font-size: 48px;
  font-weight: 600;
  text-shadow: 3px 3px rgba(30, 30, 70, 0.2);
}
